export default (state = {isLoading: true}, {type, payload}) => {
    switch (type) {
        case 'LOADING':
            return {...state,isLoading: payload};
        case 'LOADING_COMPLEATE':
            return {...state,isLoading: payload};
        case 'LOADING_FAIL':
            return {...state,isLoading: false, warning: payload};
        case 'FILTER':
            return {...state,filter: payload};
        default:
            return state;
    }
}